import React from "react"
import WinnersStyledBackgroundSection from "../components/winners-background-image"
import Layout from "../components/layout"
import Seo from "../components/seo"
import Img from "gatsby-image"
import Link from "gatsby-link"
import { graphql } from "gatsby"

const Awards = ({ data }) => (
  <Layout>
    <Seo title="Winners" />
    <WinnersStyledBackgroundSection />
    <div className="image-row winners">
      <div className="image-wrapper">
        <Img
          fluid={data.huber.childImageSharp.fluid}
          alt="todo"
          style={{ width: `100%` }}
        />
      </div>
      <div className="image-row-textbox">
        <div className="name">Zachary Huber</div>
        <div className="title mb-4">2022 Student Media Award Winner</div>
        <p className="copy">
          Zachary Huber, a recent graduate of the University of Florida in
          Gainesville, was named the collegiate winner, for his story{" "}
          <Link
            to="https://www.alligator.org/article/2021/09/former-florida-womens-basketball-players-detail-abuse-under-coach-newbauer"
            style={{ color: `white`, textDecoration: `underline` }}
          >
            “Former Florida women’s basketball players detail abuse under Coach
            Newbauer”
          </Link>
          , an exhaustive account of emotional and physical abuses endured by
          team members that was published in the Independent Florida Alligator.
          Judges praised Huber’s sourcing and noted that his work “took bravery
          and excellence against the odds.”
        </p>
      </div>
    </div>
    <div className="image-row winners">
      <div className="image-wrapper">
        <Img
          fluid={data.webster.childImageSharp.fluid}
          alt="todo"
          style={{ width: `100%` }}
        />
      </div>
      <div className="image-row-textbox">
        <div className="name">Richard Webster</div>
        <div className="title mb-4">2022 Professional Award Winner</div>
        <p className="copy">
          New Orleans-based investigative journalist Richard Webster, in
          partnership with WWNO/WRKF and ProPublica’s Local Reporting Network,
          received the professional prize for the series{" "}
          <Link
            to="https://www.propublica.org/series/unwatched"
            style={{ color: `white`, textDecoration: `underline` }}
          >
            “Unwatched”
          </Link>
          , which scrutinizes Louisiana’s Jefferson Parish Sheriff’s Office for
          civil rights violations and a lack of oversight in use of force cases,
          particularly against Black residents. Judges called the series
          “masterful,” praising Webster’s perseverance over a year of reporting.
          “The news organizations showed grit and fortitude in never giving up,”
          they said.
        </p>
      </div>
    </div>
    <div className="image-row winners">
      <div className="image-wrapper">
        <Img
          fluid={data.davison.childImageSharp.fluid}
          alt="Madeleine Davison"
          style={{ width: `100%` }}
        />
      </div>
      <div className="image-row-textbox">
        <div className="name">Madeleine Davison</div>
        <div className="title mb-4">2021 Student Media Award Winner</div>
        <p className="copy">
          Madeleine Davison, a recent graduate of Syracuse University, is the
          collegiate winner for the story{" "}
          <Link
            to="http://dailyorange.com/2020/03/nursing-center-violations-increase-profit-company-ownership/"
            style={{ color: `white`, textDecoration: `underline` }}
          >
            “Care and Capital.”
          </Link>{" "}
          Davison’s story explored nursing center violations among central New
          York for-profit facilities, and judges noted the “work and skill” were
          above and beyond student quality journalism. “Colorful, detailed
          interviews” pulled together “great use of data, information (and)
          information requests” into a well-crafted story. One judge noted, “The
          imagery in this piece cannot escape my mind.”
        </p>
      </div>
    </div>
    <div className="image-row winners">
      <div className="image-wrapper">
        <Img
          fluid={data.plohetski.childImageSharp.fluid}
          alt="Tony Plohetski"
          style={{ width: `100%` }}
        />
      </div>
      <div className="image-row-textbox">
        <div className="name">Tony Plohetski</div>
        <div className="title mb-4">2021 Professional Award Winner</div>
        <p className="copy">
          Tony Plohetski of the Austin American-Statesman/KVUE received the
          professional prize for his{" "}
          <Link
            to="https://www.statesman.com/story/news/investigations/2020/06/08/texas-police-chase-ends-death-i-cant-breathe-man-cries/3137476001/"
            style={{ color: `white`, textDecoration: `underline` }}
          >
            “Lights. Camera. Violence.”
          </Link>{" "}
          series. The panel of judges lauded Plohetski’s work on the three-story
          series, which investigated the death of a 40-year-old Black father
          while in the custody of county law enforcement, as a “great example of
          dogged reporting.” Through “extraordinary” reporting and writing,
          Plohetski created a “compelling, rich narrative that strikes just the
          right tone between sorrow and outrage.”
        </p>
      </div>
    </div>
  </Layout>
)

export default Awards

export const query = graphql`
  query {
    avatar: file(relativePath: { eq: "avatar.png" }) {
      childImageSharp {
        fluid(maxWidth: 400, quality: 100) {
          ...GatsbyImageSharpFluid_withWebp
        }
      }
    }
    huber: file(relativePath: { eq: "zach_huber.jpg" }) {
      childImageSharp {
        fluid(maxWidth: 400, quality: 100) {
          ...GatsbyImageSharpFluid_withWebp
        }
      }
    }
    webster: file(relativePath: { eq: "richard_webster.jpg" }) {
      childImageSharp {
        fluid(maxWidth: 400, quality: 100) {
          ...GatsbyImageSharpFluid_withWebp
        }
      }
    }
    davison: file(relativePath: { eq: "Davison.jpg" }) {
      childImageSharp {
        fluid(maxWidth: 400, quality: 100) {
          ...GatsbyImageSharpFluid_withWebp
        }
      }
    }
    plohetski: file(relativePath: { eq: "Plohetski.png" }) {
      childImageSharp {
        fluid(maxWidth: 400, quality: 100) {
          ...GatsbyImageSharpFluid_withWebp
        }
      }
    }
  }
`
